import React from 'react';
import { FilmType } from '../../types/film-type';
import { getFilmRuntime } from '../../utils';

type PropsTypes = {
  film: FilmType;
};

function MoviePageDetails({ film }: PropsTypes): JSX.Element {
  const {hours, minutes} = getFilmRuntime(film.runTime);

  return (
    <div className="film-card__text film-card__row">
      <div className="film-card__text-col">
        <p className="film-card__details-item">
          <strong className="film-card__details-name">Director</strong>
          <span className="film-card__details-value">{film.director}</span>
        </p>
        <p className="film-card__details-item">
          <strong className="film-card__details-name">Starring</strong>
          <span className="film-card__details-value">
            {
              film.starring.map((actor) => (
                <React.Fragment key={actor}>
                  {actor}<br />
                </React.Fragment>))
            }
          </span>
        </p>
      </div>

      <div className="film-card__text-col">
        <p className="film-card__details-item">
          <strong className="film-card__details-name">Run Time</strong>
          <span className="film-card__details-value">{`${hours}h ${minutes}m`}</span>
        </p>
        <p className="film-card__details-item">
          <strong className="film-card__details-name">Genre</strong>
          <span className="film-card__details-value">{film.genre}</span>
        </p>
        <p className="film-card__details-item">
          <strong className="film-card__details-name">Released</strong>
          <span className="film-card__details-value">{film.released}</span>
        </p>
      </div>
    </div>
  );
}

export default MoviePageDetails;
